.home {
  .homeContent {
    background: linear-gradient(100deg, $color-2 60%, transparent 100%);
    width: 100%;
    min-height: 500px;
    border-radius: $borderRadius;
    position: relative;
    overflow: hidden;

    &:after {
      content: '';
      position: absolute;
      height: 100%;
      width: inherit;
      background: url('../../media/Labo.jpg') no-repeat right/cover;
      background-size: contain;
      z-index: -1;
      filter: brightness(40%);
    }

    .content {
      position: absolute;
      top: 20%;
      left: 8%;
      font-family: $font-1;

      h1 {
        font-size: 5rem;
        font-family: $font-3;
        color: $black;
        text-transform: uppercase;
        text-shadow: 3px 6px 2px $color-3;
      }
      h2 {
        font-size: 2.5rem;
        color: $color-3;
        text-shadow: 1px 4px 1px $black;
      }
      h3 {
        font-size: 2rem;
        padding: 1.5rem 0 7rem;
        color: $color-4;
        text-shadow: 1px 4px 1px $black;
      }
      h4 {
        font-size: 2.5rem;
        text-decoration: line-through;
        color: $color-3;
        text-shadow: 1px 4px 1px $black;
      }

      .pdf {
        a {
          font-size: 1.5rem;
          background: $color-3;
          padding: 1rem 2rem;
          border-radius: $borderRadius;
          box-shadow: 0 5px 1px rgb(15, 15, 15);
          transition: 0.2s ease;

          &:hover {
            background: $color-4;
          }
        }
      }
    }
  }
}
@media screen and (max-width: 850px){
  .homeContent {
    margin: 0 !important;
  }
}
@media screen and (max-width:580px){
  .homeContent {
    &:after {
    background: url('../../media/Labo.jpg') no-repeat 60% 50%/cover !important;
    }
    h1 {
      font-size: 3rem !important;
    }
    h2 {
      font-size: 1.8rem !important;
    }
  }
}